*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.box {
  display: flex;
  float: right;
  justify-content: center;
  align-items: center;
  outline: none;
  z-index: 15; }

.hamburger {
  cursor: pointer;
  border: solid 2.5px whitesmoke;
  border-radius: 5px;
  outline: none;
  background: none;
  transition: 0.3s;
  background-color: #0c3f63;
  z-index: 3; }

.hamburger__line {
  display: block;
  width: 45px;
  height: 5px;
  margin: 10px;
  border-radius: 50px;
  background: whitesmoke;
  transition: 0.3s;
  z-index: 3; }

.cross .hamburger__line:first-child {
  -webkit-transform: translateY(15px) rotate(-40deg) scale(1.2);
          transform: translateY(15px) rotate(-40deg) scale(1.2);
  transition: 0.3s;
  z-index: 3; }

.cross .hamburger__line:nth-child(2) {
  -webkit-transform: translateX(55px);
          transform: translateX(55px);
  opacity: 0;
  transition: 0.3s;
  z-index: 3; }

.cross .hamburger__line:last-child {
  -webkit-transform: translateY(-15px) rotate(40deg) scale(1.2);
          transform: translateY(-15px) rotate(40deg) scale(1.2);
  transition: 0.3s;
  z-index: 3; }

@media screen and (min-width: 551px) {
  .box {
    display: none; } }

@media screen and (max-width: 550px) {
  .box {
    margin-left: 85%;
    margin-top: -19%; } }

