/* Style the buttons that are used to open and close the accordion panel */
.accordion {
  background-color: #eee;
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  text-align: left;
  border: none;
  outline: none;
  transition: 0.4s;
  font-family: 'Montserrat', sans-serif; }

.style-boutton button {
  background-color: #0c3f63;
  width: 105%;
  border: 1px solid rgba(12, 63, 99, 0.75);
  padding: 3%;
  color: white;
  outline: none; }

.question-text {
  font-family: 'Montserrat', sans-serif;
  width: 95%; }

.buttonTrue {
  border-radius: 25px; }

.buttonFalse {
  background-color: rgba(12, 63, 99, 0.75) !important; }

/*----------Animation croix -------*/
.closed .vertical {
  transition: all 0.5s ease-in-out;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg); }

.closed .horizontal {
  transition: all 0.5s ease-in-out;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  opacity: 1; }

.closed .arrow {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition-duration: 0.3s; }

.opened {
  opacity: 1; }
  .opened .vertical {
    transition: all 0.5s ease-in-out;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg); }
  .opened .horizontal {
    transition: all 0.5s ease-in-out;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    opacity: 0; }
  .opened .arrow {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    transition-duration: 0.3s; }

.horizontal {
  float: right;
  background-color: white;
  width: 28px;
  height: 2px;
  margin-top: -3.5%; }

.vertical {
  float: right;
  background-color: white;
  width: 2px;
  height: 30px;
  margin-top: -5.5%;
  margin-right: 1.6%; }

/*---------Fin animation -------*/
.buttonFalse {
  border-radius: 25px 25px 0 0; }

.style-boutton button:target {
  border-radius: 25px 25px 0 0; }

